import React from 'react';
import PropTypes from 'prop-types';
import {Panel, PanelHeader, Group, Gallery, Div, Button, platform, PanelHeaderContent, HeaderButton, IOS, Avatar, itemStyle} from '@vkontakte/vkui';
import connect from '@vkontakte/vk-connect';

import '@vkontakte/vkui/dist/vkui.css';

import author from '../author/author.json';
import vkuiConnect from '@vkontakte/vkui-connect';

import Icon28ChevronBack from '@vkontakte/icons/dist/28/chevron_back';
import Icon24Back from '@vkontakte/icons/dist/24/back';
import Icon24ReplyOutline from '@vkontakte/icons/dist/24/reply_outline';
import Icon24Newsfeed from '@vkontakte/icons/dist/24/newsfeed';

import './books.css';


class AuthorDetail extends React.Component {
	constructor (props) {
		super(props);

		this.state = {
			bookText: null,
			activeStory: "home",
			check: false,
			activePanel: 'feed',
      history: ['feed'],
			page: this.props.bookStartId
		};
	}
	
share = () => { 
	  	connect.send("VKWebAppShowWallPostBox", {"message": `Привет! Я только что узнал, что ${author[this.props.bookId].bio} Подробнее в сервисе - vk.com/app7116412`  });
	}

	backauthor = (e) => { 
		this.setState({ activeStory: e.currentTarget.dataset.to })
		window.scrollTo(0,0)
	}

	goBack = () => {
		const history = [...this.state.history];
		history.pop();
		const activePanel = history[history.length - 1];
		if (activePanel === 'feed') {
		  vkuiConnect.send('VKWebAppDisableSwipeBack');
		}
		this.setState({ history, activePanel });
	  }

	  goForward = (activePanel) => {
		const history = [...this.state.history];
		history.push(activePanel);
		if (this.state.activePanel === 'feed') {
		  vkuiConnect.send('VKWebAppEnableSwipeBack');
		}
		this.setState({ history, activePanel });
	  }

	
	render () {
		const osname = platform();
		return (
			<Panel id={this.props.id}>
				<PanelHeader 
			left={<HeaderButton onClick={this.props.backauthor} data-to="feed">
				{osname === IOS ? <Icon28ChevronBack/> : <Icon24Back/>}
			</HeaderButton>}>
			<PanelHeaderContent
        status={author[this.props.bookId].title}
        >
			Рецептник
		</PanelHeaderContent>
		</PanelHeader>
		<Group className="videl" title="Фотографии">
		<Gallery
                slideWidth="custom"
                style={{ height: 210 }}
              >
                <img type="image" className="ico-author" src={author[this.props.bookId].path}/>
              </Gallery>
				</Group>
				<Group title="Описание">
         <Div>
				 {author[this.props.bookId].bio}
				 </Div>
				</Group>	
				 <Div>
       <Button size="xl" before={<Icon24Newsfeed/>} onClick={this.share}level="secondary">Поделиться</Button>
     </Div>	
			</Panel>
        );
    }
}
AuthorDetail.propTypes = {
	id: PropTypes.string.isRequired,
	go: PropTypes.func.isRequired,
	backauthor: PropTypes.func.isRequired,
	share: PropTypes.func,
};


export default (AuthorDetail);
