const blueBackground = {
  backgroundColor: 'var(--accent)'
};


function openTab(url) {

// Create link in memory

let a = window.document.createElement("a");

a.target = '_blank';

a.href = url;

// Dispatch fake click

let e = window.document.createEvent("MouseEvents");

e.initMouseEvent("click", true, true, window, 0, 0, 0, 0, 0, false, false, false, false, 0, null);

a.dispatchEvent(e);

};


const itemStyle = {
    flexShrink: 0,
    width: 80,
    height: 94,
    display: 'flex',
    flexDirection:
    'column',
    alignItems: 'center',
    fontSize: 12
  };




const book = Object.keys(books).map(i => { 
	return books[i]; 
	})


class Home  extends React.Component {
	constructor (props,) {
	  super(props);
  
		this.state = {
			activeStory: 'discover',
			bookId: null,
			popout: null,
			search: '',
			contextOpened: true,
			mode: 'all',
			activePanel: 'laod',
		    activeModal: null,
		    text: '',
            snackbar: null,
            modalHistory: [], 
            history: ['feed']
		};
    this.onChange = this.onChange.bind(this);
    this.onStoryChange = this.onStoryChange.bind(this);
	this.toggleContext = this.toggleContext.bind(this);
    this.select = this.select.bind(this);
   }


   componentDidMount() {
		connect.subscribe((e) => {
			switch (e.detail.type) {
				case 'VKWebAppGetUserInfoResult':
					this.setState({ fetchedUser: e.detail.data });
					break;
				case 'VKWebAppGetClientVersionResult':
					this.setState({ platform: e.detail.data.platform });
					if (this.state.platform === 'android') {
						connect.send('VKWebAppSetViewSettings', {
							status_bar_style: 'light',
							action_bar_color: '#ff0000'
						});
						}
					break;
				default:
					console.log(e.detail.type);
			}
		});
		connect.send('VKWebAppGetUserInfo', {});
	}



    
	goBack = () => {
    const history = [...this.state.history];
    history.pop();
    const activePanel = history[history.length - 1];
    if (activePanel === 'feed') {
      vkuiConnect.send('VKWebAppDisableSwipeBack');
    }
    this.setState({ history, activePanel });
	}
	
	goForward = (activePanel) => {
    const history = [...this.state.history];
    history.push(activePanel);
    if (this.state.activePanel === 'feed') {
      vkuiConnect.send('VKWebAppEnableSwipeBack');
    }
    this.setState({ history, activePanel });
    }

	toggleContext () {
		this.setState({ contextOpened: !this.state.contextOpened });
	  }
	
	select (e) {
		const mode = e.currentTarget.dataset.mode;
		this.setState({ mode });
		requestAnimationFrame(this.toggleContext);
	}

	onChange (search) { this.setState({ search }); }
	
	get book () { 
		const search = this.state.search.toLowerCase();
		return book.filter(({title}) => title.toLowerCase().indexOf(search) > -1);
	}

	go = (e) => { 
		this.setState({ activeStory: e.currentTarget.dataset.to })
	}

	backauthor = (e) => { 
		this.setState({ activeStory: e.currentTarget.dataset.to })
		window.scrollTo(0,0)
	}

    onStoryChange (e) {
		this.setState({ popout: <ScreenSpinner /> });
        setTimeout(() => { this.setState({ popout: null }) }, 2000);
		this.setState({ activeStory: e.currentTarget.dataset.story })
		window.scrollTo(0,0)
	}

	selectBook = id => {
		this.setState({ bookId: id, activeStory: "bookDetail"})
		window.scrollTo(0,0)
	}
	
	selectAuthor = id => {
		this.setState({ bookId: id, activePanel: "authorDetail"})
		window.scrollTo(0,0)
	}
	render () {
     
		return (
			<Epic activeStory={this.state.activeStory} tabbar={
				this.state.activeStory !== 'bookDetail' &&
				this.state.activeStory !== 'authorDetail' &&
			<Tabbar>
			<TabbarItem
				ondblclick={this.props.go} data-to="discover"
				onClick={this.onStoryChange}
				selected={this.state.activeStory === 'discover'}
				data-story="discover"
			><Icon24List/></TabbarItem>
			<TabbarItem
				ondblclick={this.props.go} data-to="info"
				onClick={this.onStoryChange}
				selected={this.state.activeStory === 'info'}
				data-story="info"
			><Icon24Newsfeed/></TabbarItem>
			</Tabbar>
		}>
			<View id="feed" activePanel="feed" >
			<Panel id="feed">
				<PanelHeader>Рецепты</PanelHeader>
							<Group title={`Расписание на `} className="Header__content">
					<List>
						{
							Object.keys(books).map(i => <Cell
								onClick={() => this.selectBook(i)}
								key={i}

							>
							<span>{books[i].title}</span>
							<span className="disable-selection text">Кухня: {books[i].kuxnya} </span>
							<span className="disable-selectionn text">Категория: {books[i].category}</span>
							</Cell>

							)
						}
					</List> 
				</Group>
				 <Div>
     </Div>
			</Panel>
			</View>

			<View id="discover" activePanel="discover">
			<Panel id="discover">
			<PanelHeader>Расписание</PanelHeader>
				<Search value={this.state.search} onChange={this.onChange}/>
				<List>
				{ 
this.book.map((book,r) => ( 
	<Cell 
	key={book.id} 
	onClick={() => this.selectBook(book.id)} 
	>{book.title}</Cell> 
	)).sort((a,b) =>{ return a-b })}
				</List>
				 <Group title="">
              <List>
                <Cell
                  before={<Avatar src="https://sun9-63.userapi.com/c845322/v845322649/148fbc/TRN_cS13ogE.jpg"size={72} />}
                  size="l"
                  description="Официальная группа"
                  bottomContent={
                    <div style={{ display: 'flex' }}>
                      <Button onClick={() => openTab("https://vk.com/bvsch947")} size="m">Перейти</Button>
                    </div>
                  }
                >
                 ГБОУ Школа №947</Cell>
              </List>
            </Group>
             <Footer><Link onClick={() => openTab("https://vk.com/pukhn")}>Разработчик</Link></Footer>
				</Panel>
			</View>
			<View id="info" activePanel="info">
			<Panel id="info">
			<PanelHeader>Новости</PanelHeader>
                <br /> 
                <br /> 
                <br /> 
                <br /> 
                <br /> 
                <br /> 
           <Footer style={{color: "#000000"}}> <h3>Тут пока ничего нет. </h3></Footer>

				</Panel>
			</View>

			<View id="bookDetail" activePanel="bookDetail" onSwipeBack={() => this.setState({activePanel: "feed"})}>
				<BookDetail id="bookDetail" openBase={this.openBase}go={this.go} bookId={this.state.bookId} bookStartId={1} />
			</View>
			<View id="authorDetail" activePanel="AuthorDetail" history={this.state.history} onSwipeBack={this.goBack} >
				<AuthorDetail id="authorDetail" backauthor={this.backauthor} bookId={this.state.bookId} bookStartId={1} />
			</View>
		</Epic>
	)}
};

Home.propTypes = {
	id: PropTypes.string.isRequired,
	go: PropTypes.func.isRequired,
	backauthor: PropTypes.func.isRequired,
	fetchedUser: PropTypes.shape({
		photo_200: PropTypes.string,
		first_name: PropTypes.string,
		last_name: PropTypes.string,
		city: PropTypes.shape({
			title: PropTypes.string,
		}),
	}),
};

export default Home;